.MoneyEx {
  @apply h-screen container mx-auto w-11/12 lg:w-5/6 xl:w-4/6;

  .gridContainer {
    @apply grid grid-cols-1 gap-3 sm:gap-6;
    @apply mt-2 sm:mt-5 lg:mt-10;
  }

  .inputOutput {
    // @apply row-span-1 col-span-4;
    @apply bg-white rounded-lg shadow-xl;
    @apply flex flex-row;
    @apply p-5 sm:p-10 xl:px-20 sm:space-x-1;
    @apply text-xl sm:text-3xl font-extrabold;

    .codeSymbolGroup{
      @apply w-28 mt-1.5 sm:mt-0.5;
      @apply text-lime-600 text-opacity-50;
    }

    .inputField {
      @apply outline-none w-full -mt-0.5 sm:-mt-1 sm:w-5/6;
      @apply text-gray-600 font-extrabold tracking-wide placeholder:text-gray-300 placeholder:text-base sm:placeholder:text-3xl;
      // @apply placeholder-shown:text-gray-100;
    }
  }

  .favList {
    // @apply row-span-2 col-span-4;
    // @apply row-span-2 col-span-2;
    @apply grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 2xl:grid-cols-4 gap-2 sm:gap-4 p-2 sm:p-5;
    @apply border bg-gray-100 rounded-lg shadow-xl;
    
    .itemContainer {
      @apply m-1 p-2 flex flex-row;
      @apply border rounded-xl bg-white;
      
      &:hover {
        @apply cursor-pointer;
      }
      .valueNameGroup {
        @apply flex flex-col ml-2 justify-center;

        .name {
          @apply text-gray-400 text-xs sm:text-sm;
        }
        .exRate {
          @apply text-base sm:text-xl tracking-wide;
        }
      }
      .codeFlagGroup {
        @apply h-12 w-24 rounded-xl bg-gray-100 p-3 text-center scale-90;
        @apply flex flex-row justify-center;
        @apply text-base font-extrabold tracking-wide text-gray-600;
      }
    }
  }

  .fullList {
    @apply row-span-6 col-span-2;
    @apply grid grid-cols-1 md:grid-cols-4 lg:grid-cols-5 xl:grid-cols-7;
    @apply max-h-fit overflow-y-auto;
    @apply border bg-gray-100 rounded-lg shadow-xl;
    @apply text-xs;
  
    .itemContainer {
      @apply h-28 m-1 p-2;
      @apply border bg-white;
  
      &:hover {
        @apply bg-slate-500 cursor-pointer;
      }
      .codeFlagGroup {
        @apply p-1 rounded-full bg-gray-200;
      }
      .name {
        @apply mt-1 text-gray-700;
      }
    }
  }

  .starColor {
    filter: brightness(1) grayscale(1);
    // filter: invert(48%) sepia(79%) saturate(2476%) hue-rotate(86deg) brightness(118%) contrast(119%);
  }
}
